<template>
    <div class="home" :style="{ 'background-image': `url(images/room${currentRoomNo+1}.jpg)` }">
        <div v-if="rooms" class="container">
            <div class="nav">
                <router-link
                    :to="`/room/${room.id}`"
                    v-for="(room,idx) in rooms"
                    :key="room.id"
                    @mouseenter.native="currentRoomNo=idx"
                >{{room.name}}</router-link>
            </div>
            <div class="roomInfo">
                <p class="roomInfo-no">0{{currentRoomNo+1}}</p>
                <p class="roomInfo-name">{{currentRoomName}}</p>
            </div>
        </div>
        <Spin v-else fix>
            <Icon type="ios-loading" size="18" class="spin-icon-load"></Icon>
            <div>Loading</div>
        </Spin>
    </div>
</template>

<script>
let preloadImgs = [];

import axios from 'axios';
export default {
    name: 'home',
    data() {
        return {
            rooms: null,
            currentRoomNo: 0,
        }
    },
    computed: {
        currentRoomName() {
            return this.rooms[this.currentRoomNo] && this.rooms[this.currentRoomNo].name;
        }
    },
    methods: {
        getRooms() {
            axios.get('https://challenge.thef2e.com/api/thef2e2019/stage6/rooms', {
                headers: {
                    Authorization: process.env.VUE_APP_TOKEN,
                    accept: 'application/json'
                },
            })
                .then(res => {
                    if (res.data.success) {
                        let loadedImgs = 0;
                        for (let i = 0; i < 6; i++) {
                            preloadImgs[i] = new Image();
                            preloadImgs[i].onload = () => {
                                loadedImgs++;
                                if (loadedImgs === 6) {
                                    this.rooms = res.data.items;
                                }
                            };
                            preloadImgs[i].src = `images/room${i+1}.jpg`;
                        }
                    }
                })
                .catch(err => console.log(err));
        },
    },
    created() {
        this.getRooms();
    },
}
</script>

